:root{
    --header-height: 50px;
}

body{
    padding-top: calc(var(--header-height) + 30px);
}

.header{
    background: var(--background-color);
    border-bottom: 1px solid rgba(var(--text-color-rgb), 0.3);
    height: var(--header-height);
    margin-bottom: 10px;
    padding: 10px;
    overflow: hidden;
    display: grid;
    align-content: center;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    justify-items: center;
    transition: 0.1s ease-in;
    z-index: 1000;

    .logo-container{
        width: fit-content;
        height: 100%;
        overflow: hidden;
        cursor: pointer;

        img{
            height: 100%;
            -webkit-user-drag: none;
            user-select: none;
        }
    }

    .links-container{
        display: grid;
        grid-template-columns: var(--columns);

        .link{
            --color: var(--text-color);
            width: fit-content;
            padding: 0 15px;
            cursor: pointer;
            color: var(--color);
            
            &::after{
                content: "";
                display: block;
                width: 0px;
                height: 1px;
                margin: 0 auto;
                background: var(--color);
                transition: all 0.2s ease;
            }

            &:hover{
                &::after{
                    width: 100%;
                }
            }
        }
    }

    &.floatting{
        position: fixed;
        top: 0;
        width: 100%;
        left: 0;
    }

    &.floatting-hidden{
        position: fixed;
        top: -100px;
    }
}