@font-face {
    font-family: 'NeoMatrix';
    src: url(../../public/fonts/NeomatrixCode.otf);
}

body{
    background-color: var(--background-color);
    color: var(--text-color);
    font-family: "NeoMatrix";
}

::-webkit-scrollbar{
    display: none;
}

a{
    text-decoration: underline;
    text-decoration-color: var(--neon-green);
    cursor: pointer;

    &:hover{
        background-color: var(--neon-green);
        color: black;
    }
}

.textSpain{
    color: transparent;
    background-clip: text !important;
    background: linear-gradient(90deg, rgba(255,0,0,1) 0% 30%, rgba(255,255,0,1) 30% 70%, rgba(255,0,0,1) 70% 100%);
}