:root{
    --white-shadow: 2px 2px 0px white;
}

*{
    z-index: 10;
}

#particles{
    --top: calc(var(--header-height) * -1);
    --left: 0;
    --trns-x: 0;
    --trns-y: 0;
    z-index: 0;
    overflow: hidden;
    position: fixed;
    height: 150%;
    width: 150%;
    top: var(--top);
    left: var(--left);
    transform: translate(var(--trns-x),var(--trns-y));
    opacity: 0.1;
    pointer-events: none;

    .particle{
        position: absolute;
        top: var(--y-pos);
        left: var(--x-pos);
        transition: all 0.2s;
        border-color: var(--color);

        &.triangle{
            width: 0;
            height: 0;
            border-left: 10px solid transparent;
            border-right: 10px solid transparent;
            border-bottom: 20px solid red;
        }

        &.square{
            width: 20px;
            height: 20px;
            background-color: var(--color);
        }
        
        &.circle{
            width: 20px;
            height: 20px;
            background-color: var(--color);
            border-radius: 100%;
        }
    }
}


.homesection{
    font-size: 30px;
    margin: 50px auto 100px auto;
    max-width: 70%;
    .title{
        width: fit-content;
        border-bottom: 5px double white;
        margin-bottom: 15px;
    }
    .content{
        font-size: 17px;
        color: var(--text-color);
        text-decoration: none;
        line-height: 25px;
    }
}

#inicio{
    display: flex;
    -webkit-box-pack: center;
    -webkit-box-align: center;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;

    .beforeTitle, .miNombre, .subtitle{
        text-align: start;
    }
    .miNombre{
        font-size: 100px;
        color: var(--neon-green);
        text-shadow: var(--white-shadow);
    }
    .subtitle{
        font-size: 18px;
    }
}

#about{
    color: var(--neon-violet);
    text-align: start;
    .title{
        width: 100%;
        border-color: var(--neon-violet);
    }
}

#skills{
    color: var(--neon-red);
    .title{
        border-color: var(--neon-red);
    }
    .content{
        .skillSection{
            width: fit-content;
            margin-right: 100px;
            float: left;
            .skillSectionTitle{
                padding-left: 10px;
                border-left: 2px solid #fff5;
            }
            .skillSectionSubtitle{
                padding-left: 10px;
                font-size: 15px;
                color: #fff5;
            }
            .skill{
                padding: 10px;
                .skillTitle{
                    padding-left: 20px;
                    position: relative;
                    &::before{
                        content: "";
                        position: absolute;
                        left: 0;
                        top: 50%;
                        transform: translateY(-50%);
                        display: block;
                        width: 10px;
                        height: 10px;
                        border-radius: 10px;
                        background-color: white;
                    }
                }
                .skillDescription{
                    font-size: 16px;
                    color: rgb(133, 133, 133);
                }
            }
        }
    }
}